import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import {Router, hashHistory} from 'react-router';
import routes from './routes';

const app = document.getElementById('app');

ReactDOM.render(
    <Router history={hashHistory} routes={routes}/>,
    app
);



